
'use client';

import AppLayout from "@/components/app-layout";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Avatar, AvatarImage, AvatarFallback } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import { useAuthState } from "react-firebase-hooks/auth";
import { auth } from "@/lib/firebase";
import { Loader2 } from "lucide-react";

export default function ProfilePage() {
    const [user, loading] = useAuthState(auth);

    if (loading) {
        return <AppLayout><div className="flex justify-center items-center h-full"><Loader2 className="h-8 w-8 animate-spin" /></div></AppLayout>;
    }

    if (!user) {
        return <AppLayout><p>Please sign in to view your profile.</p></AppLayout>;
    }

    return (
        <AppLayout>
            <div className="max-w-2xl mx-auto space-y-6">
                 <div className="mb-6">
                    <h1 className="text-3xl font-bold font-headline">My Profile</h1>
                    <p className="text-muted-foreground">
                        View and manage your account details.
                    </p>
                </div>
                <Card>
                    <CardHeader>
                        <CardTitle>Account Information</CardTitle>
                        <CardDescription>This is your personal information.</CardDescription>
                    </CardHeader>
                    <CardContent className="space-y-4">
                        <div className="flex items-center space-x-4">
                             <Avatar className="h-24 w-24">
                                <AvatarImage src={user.photoURL ?? "https://placehold.co/100x100"} alt={user.displayName ?? 'User'} />
                                <AvatarFallback>{user.displayName?.charAt(0) ?? 'U'}</AvatarFallback>
                            </Avatar>
                            <div className="space-y-1">
                                <h2 className="text-2xl font-semibold">{user.displayName || "Anonymous User"}</h2>
                                <p className="text-muted-foreground">{user.email}</p>
                            </div>
                        </div>
                        <Button variant="outline">Edit Profile</Button>
                    </CardContent>
                </Card>
            </div>
        </AppLayout>
    );
}
